var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container-fluid"},[_c('h1',{staticClass:"nav-icon"},[_c('a',{attrs:{"href":"/"}},[_c('i',{staticClass:"bi bi-house"})])]),_c('div',{staticClass:"logo"},[_c('img',{attrs:{"src":require("../assets/french/french_logo.png")}})]),_c('div',{staticClass:"article"},[_c('p',{staticClass:"intro"},[_vm._v(" Il y a 3 ans que j’ai commencé à apprendre cette belle langue et je suis fier de dire que j’ai plus de confiance pour écrire et parler en français. Notamment, je viens d’obtenir mon certificat compétence - langue et culture français de l’Université de McGill. ")]),_c('h5',[_vm._v("De quelle façon j'ai appris le français?")]),_c('p',[_vm._v(" Pendant ma participation au programme d'échange étudiant à l' Université de McGill à Montréal, Québec, mon intérêt pour la langue a été piqué. Je dis souvent à mes amis que se promener le long des rues de Montréal, c’est semblable à lire un dictionnaire bilingue. À chaque côté de la rue, tu peux regarder les panneaux à deux langages en écoutant les francophones qui intègrent les jeux des mots en anglais dans leur parole. C'était une expérience très cool et inoubliable. ")]),_c('p',[_vm._v(" À mon avis, être allé à Montréal pendant l’hiver a été une double tranchant. D’une part, il y avait beaucoup de nuits où il était impossible de sortir à cause des tempêtes de neige. Mais, d'autre part, ils m’ont offert l'occasion d' apprendre le français. Grâce à ces temps-là, j’ai été exposé aux rudiments de la langue français. ")]),_c('p',[_vm._v(" Après être retourné à Singapour, j’ai continué à apprendre le français en faisant de l'auto-apprentissage. Mais, mon français vraiment atteint un niveau plus haut juste récemment quand j’ai suivi le cours intensif de français à McGill. Je l’ai trouvé très compréhensive parce qu'il m'a donné des opportunités de m’exprimer en écriture et en parole. En outre, j’ai acquis de nombreuses expressions et plus de vocabulaire, particulièrement dans les domaines de la communication, du travail et des arts. ")]),_c('p',[_vm._v(" J'espère que dans le futur, je serais capable de perfectionner mon français afin que je me sente à l’aise de l'utiliser dans un environnement de travail. ")]),_c('h5',[_vm._v("Qu'est-ce que j’aime dans la langue français?")]),_c('p',[_vm._v(" Selon moi, le français est une langue poétique. Son rythme est très régulier et la manière dont il apporte certaines modifications au niveau de la prononciation pour que la phrase soit lyrique, me semble très intéressante. ")]),_c('p',[_vm._v(" J’aime aussi la manière dont une seule idée peut être représentée dans les sens différents entre le français et l’anglais. Pour moi, le processus de trouver ces nuances, bien que fastidieuse, est vraiment thérapeutique. De plus, ce processus me permet de me faire une idée générale de la façon dont les francophones pensent. Par exemple, la phrase ‘il vaut mieux que tu…’ est une phrase commune qui établit le locuteur d’exprimer ses opinions. Mais la traduction équivalente en anglais pourrait être interprétée comme une confrontation. ")]),_c('p',[_vm._v(" La dernière raison pour laquelle j’aime le français c’est qu'une bonne compréhension de celui-ci me permettra d’avoir une gamme d'œuvres littéraires à ma disposition. Il serait mieux qu’une histoire puisse être lue dans sa langue originale. Alors, j’aimerais avoir l'opportunité de lire les œuvres fameuses comme Le Petite Prince et L’Etranger en français à l’avenir. ")]),_c('h5',[_vm._v("Quelles techniques j’utilise pour améliorer mon français?")]),_c('p',[_vm._v(" J’aime regarder les vidéos pendant mon temps libre. Parmi mes chaînes préférées, je regarde souvent le site Allemand-Français ARTE. Il produit souvent des reportages sur les phénomènes sociaux contemporains qui concernent tout le monde. Par ailleurs, il y a aussi la chaîne qui s’appelle Nota Bene dont le contenu présente des similitudes avec ARTE. Mais, celle-ci continent les discussions sur les origines de ces phénomènes, particulièrement en France. Alors, cette chaîne me présente une opportunité de bien apprécier la culture français. ")]),_c('p',[_vm._v(" En outre, j’aime lire les livres en français. Parce que ma compréhension de français est un peu limitée maintenant, je lis souvent des histoires pour les jeunesse comme Harry Potter ou des livres adultes qui sont moins compliqués que ceux de Christine Agatha. ")]),_c('p',[_vm._v(" Mais, jusqu'à maintenant, j’avais peu d'opportunités pour pratiquer le français oralement. C’est parce que les gens de mon entourage parlent toujours en anglais. En conséquence, j’aimerais trouver une façon pour que je puisse le pratiquer. Peut-être que je vais essayer d'utiliser les applications dans lesquelles je peux converser avec les francophones. ")]),_c('h5',[_vm._v("Mes conseils pour apprendre une nouvelle langue")]),_c('p',[_vm._v(" Apprentissage d’une langue est une chose qu’on ne peut pas forcer. Cela sera arrivé avec le temps. Mon ancien professeur de français m’a donné un conseil que je trouve très utile. Elle m’a recommandé de ne pas me précipiter pour appuyer sur le bouton de pause chaque fois que je ne connaissais pas un mot dans les vidéos que je regarde. Après avoir adopté son conseil, je les ai trouvé plus plaisantes à regarder car je pourrais comprendre les sujet entière facilement, sans interruptions. ")]),_c('p',[_vm._v(" C’est la même chose pour les livres aussi! Essaie-toi d'utiliser le contexte pour deviner le sens du mot. Je pense que cette compétence pourra t’aider avec ta communication en français aussi parce qu’il est souvent qu’on ne connaît pas tous les mots d'une parole. ")])])])}]

